  --min-height: 35px;
}
:host .header-md {
  box-shadow: none;
}
:host ion-card {
  border-radius: 0;
}

highcharts-chart {
  overflow: hidden;
  width: 100% !important;
  display: flex;
  margin: 0 auto;
  height: 300px !important;
}

.font18 {
  font-size: 18px;
  margin: 10px 0;
}

ion-list {
  background-color: #d0d1eb !important;
  padding: 0px;
}
ion-list ion-badge {
  font-weight: normal;
}

.list-color {
  padding: 10px;
  --background-color: #fff !important;
  background-color: #fff !important;
}

.box-wrapper {
  --background-color: #eef1f2 !important;
  background-color: #eef1f2 !important;
  margin-bottom: 10px;
  padding: 7px;
  border-radius: 4px !important;
  box-shadow: none !important;
  border: 1px solid #e3e6ea;
  display: flex;
  align-items: center;
}
.box-wrapper .box {
  flex: 1;
  font-size: 12px;
  color: var(--dark-color);
}
.box-wrapper .box.left-b span {
  display: block;
}
.box-wrapper .box.left-b .name {
  color: var(--black-color);
  font-weight: bold;
  font-size: 16px;
}
.box-wrapper .box.right-b {
  display: flex;
  justify-content: end;
}
.box-wrapper .box.right-b ion-button {
  font-size: 12px;
  --box-shadow: none !important;
  text-transform: capitalize;
  letter-spacing: 0;
}
.box-wrapper .box icon-badge {
  font-weight: normal;
}

.date-header {
  padding-left: 15px;
}
.date-header ion-card-subtitle {
  text-transform: initial;
}
.date-header ion-card-subtitle span + span {
  font-weight: 500;
  letter-spacing: 0;
}

ion-checkbox {
  --size: 24px;
  --checkbox-background-checked: var(--dark-color);
  font-size: 16px;
  color: var(--black-color);
}

ion-checkbox::part(container) {
  border-radius: 6px;
  border: 2px solid var(--dark-color);
}

ion-toggle {
  --track-background: #a9a7a7 !important;
  --track-background-checked: #a9a7a7 !important;
